import { h, Component } from 'preact';
import style from './style';

import EJS from 'persoo-templates/lib/embeddedjs';
import persooTemplates from 'persoo-templates';

import { convertToReactComponent } from '../../utils/templateComponentsUtils';


export default class SearchResults extends Component {
    createDefaultContentFromTemplate(template) {
        let offer = {
                variants:[{
                    templateID: template.id,
                    content: {},
                    scenarios: []
                }]
        };
        let offerContent = offer.variants[0].content;
        let offerScenarios = offer.variants[0].scenarios;

        if (template.fields) {
            for (var i = 0; i < template.fields.length; i++) {
                var field = template.fields[i];
                if (!field.id) {
                    console.error(templateID, 'fields[' + i +'].id is missing.');
                }
                if (typeof field.defaultValue === 'undefined' && field.type != 'section') {
                    console.error(templateID, 'fields[' + i +'].defaultValue is missing.');
                }
                if (field.id && typeof field.defaultValue !== 'undefined') {
                    offerContent[field.id] = field.defaultValue;
                }
            }
        } else {
            console.error(templateID, 'has no fields.')
        }
        return offer;
    }

    getSearchResultsHTML(searchResultsOfferTemplate) {
        let offer = this.createDefaultContentFromTemplate(searchResultsOfferTemplate);
        let offerContext = {
            offerID: "randomOfferID",
            locationID: "randoLocationID",
            profile: {
                db: {
                    identifications: {}
                }
            }
        };
        return persooTemplates.render(searchResultsOfferTemplate.template, offer.variants[0], offerContext);
    }

    runJavascriptInHTML() {
        // run javascripts contained in the html in iFrame context
        let scriptElements = previewDoc.body.getElementsByTagName('script');
        for (let i = 0; i < scriptElements.length; i++) {
            window.eval(scriptElements[i].innerHTML);
        }
    }

    

	render(props) {
        console.log('Render Search Results');

        const { searchResultsOfferTemplate } = props;
        let SearchComponent = null;
        if (searchResultsOfferTemplate) {
            SearchComponent = convertToReactComponent(this.getSearchResultsHTML.bind(this, searchResultsOfferTemplate));
            /* Notes:
                  - <script> element se vlozi jako rawHTML, ale neozivi (tedy nicemu nevadi}
            */
        }

		return (
			<div class={style.home}>
				<h1>Search Results</h1>
                <pre>
                    {JSON.stringify(props, false, 4)}
                </pre>
                <SearchComponent />
			</div>
		);
	}
}



